import Resolver from "./Resolver";

export default class SingletonResolver extends Resolver {
    /**
     * @type {Object|null}
     * @private
     */
    _resolved: ?Object;

    /**
     * @param args
     * @return {Object|*}
     */
    resolve(...args: any): any {
        if (this._resolved === null) {
            this._resolved = new this.service(...args);
        }

        return this._resolved;
    }
}


