export default class Resolver {
    /**
     * @type {*}
     * @private
     */
    _service: any;

    /**
     * @param {*} service
     */
    constructor(service: any) {
        this._service = service;
    }

    /**
     * @return {*}
     */
    get service(): any {
        return this._service;
    }

    /**
     * @param {*} args
     * @return {*}
     */
    resolve(...args: any): any {
        return this._service(...args);
    }
}


